import React, { useState } from 'react';
import { ResponsiveBar } from '@nivo/bar'
import Container from 'react-bootstrap/Container';
import { useSelector } from "react-redux";
import { useEffect } from 'react';
import Card from 'react-bootstrap/Card';



export default function SrimValueGraph() {

    const srimCorpValue = useSelector((state) => state.srimCorpValue)

    function numWithComma(num) {
        return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
    }

    function numWithComma(num) {
        return Math.round(num / 100000000).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')

    }

    let firstData = [
        {
            "": srimCorpValue[0][''],
            "ROE ": srimCorpValue[0][''],
            "Color": "hsl(323, 70%, 50%)",
            "10% ": srimCorpValue[0]['10'],
            "10% Color": "hsl(271, 70%, 50%)",
            "20% ": srimCorpValue[0]['20'],
            "20% Color": "hsl(325, 70%, 50%)",
        }, {
            "": srimCorpValue[1][''],
            "ROE ": srimCorpValue[1][''],
            "Color": "hsl(323, 70%, 50%)",
            "10% ": srimCorpValue[1]['10'],
            "10% Color": "hsl(271, 70%, 50%)",
            "20% ": srimCorpValue[1]['20'],
            "20% Color": "hsl(325, 70%, 50%)",
        }, {
            "": srimCorpValue[2][''],
            "ROE ": srimCorpValue[2][''],
            "Color": "hsl(323, 70%, 50%)",
            "10% ": srimCorpValue[2]['10'],
            "10% Color": "hsl(271, 70%, 50%)",
            "20% ": srimCorpValue[2]['20'],
            "20% Color": "hsl(325, 70%, 50%)",
        }
    ]

    const [valueData, setValueData] = useState(firstData)

    useEffect(() => {

        let resultData = [
            {
                "": srimCorpValue[0][''],
                "ROE ": Math.round(srimCorpValue[0][''] / 100000000),
                "Color": "hsl(323, 70%, 50%)",
                "10% ": Math.round(srimCorpValue[0]['10'] / 100000000),
                "10% Color": "hsl(271, 70%, 50%)",
                "20% ": Math.round(srimCorpValue[0]['20'] / 100000000),
                "20% Color": "hsl(325, 70%, 50%)",
            }, {
                "": srimCorpValue[1][''],
                "ROE ": Math.round(srimCorpValue[1][''] / 100000000),
                "Color": "hsl(323, 70%, 50%)",
                "10% ": Math.round(srimCorpValue[1]['10'] / 100000000),
                "10% Color": "hsl(271, 70%, 50%)",
                "20% ": Math.round(srimCorpValue[1]['20'] / 100000000),
                "20% Color": "hsl(325, 70%, 50%)",
            }, {
                "": srimCorpValue[2][''],
                "ROE ": Math.round(srimCorpValue[2][''] / 100000000),
                "Color": "hsl(323, 70%, 50%)",
                "10% ": Math.round(srimCorpValue[2]['10'] / 100000000),
                "10% Color": "hsl(271, 70%, 50%)",
                "20% ": Math.round(srimCorpValue[2]['20'] / 100000000),
                "20% Color": "hsl(325, 70%, 50%)",
            }
        ]

        setValueData(() => resultData)

    }, [srimCorpValue])

    const SrimValueGraph = ({ data }) => (
        <ResponsiveBar
            data={data}
            keys={[
                'ROE ',
                '10% ',
                '20% ',
            ]}
            indexBy=""
            margin={{ top: 50, right: 130, bottom: 50, left: 70 }}
            padding={0.3}
            groupMode="grouped"
            valueScale={{ type: 'linear' }}
            indexScale={{ type: 'band', round: true }}
            valueFormat=" >-,"
            colors={{ scheme: 'nivo' }}
            defs={[
                {
                    id: 'dots',
                    type: 'patternDots',
                    background: 'inherit',
                    color: '#38bcb2',
                    size: 4,
                    padding: 1,
                    stagger: true
                },
                {
                    id: 'lines',
                    type: 'patternLines',
                    background: 'inherit',
                    color: '#eed312',
                    rotation: -45,
                    lineWidth: 6,
                    spacing: 10
                }
            ]}
            fill={[
                {
                    match: {
                        id: 'fries'
                    },
                    id: 'dots'
                },
                {
                    match: {
                        id: 'sandwich'
                    },
                    id: 'lines'
                }
            ]}
            borderColor={{
                from: 'color',
                modifiers: [
                    [
                        'darker',
                        '1.6'
                    ]
                ]
            }}
            axisTop={null}
            axisRight={null}
            axisBottom={{
                tickSize: 5,
                tickPadding: 5,
                tickRotation: 0,
                legend: '',
                legendPosition: 'middle',
                legendOffset: 32
            }}
            axisLeft={{
                tickSize: 5,
                tickPadding: 5,
                tickRotation: 0,
                legend: '',
                legendPosition: 'middle',
                legendOffset: -65
            }}
            labelSkipWidth={12}
            labelSkipHeight={16}
            labelTextColor={{
                from: 'color',
                modifiers: [
                    [
                        'darker',
                        1.6
                    ]
                ]
            }}
            legends={[
                {
                    dataFrom: 'keys',
                    anchor: 'bottom-right',
                    direction: 'column',
                    justify: false,
                    translateX: 120,
                    translateY: 0,
                    itemsSpacing: 2,
                    itemWidth: 100,
                    itemHeight: 20,
                    itemDirection: 'left-to-right',
                    itemOpacity: 0.85,
                    symbolSize: 20,
                    effects: [
                        {
                            on: 'hover',
                            style: {
                                itemOpacity: 1
                            }
                        }
                    ]
                }
            ]}
            role="application"
            ariaLabel="Nivo bar chart demo"
            barAriaLabel={function (e) { return e.id + ": " + e.formattedValue + " in country: " + e.indexValue }}
        />
    )


    return (
        <>
            <Container>
                <Card className="text-center" border="light">
                    <Card.Body>
                        <Card.Title></Card.Title>
                        <Card.Text>
                        </Card.Text>
                        <div style={{ width: 'auto', height: '400px', margin: '0 auto' }}>
                            <SrimValueGraph data={valueData} />
                        </div>
                    </Card.Body>
                </Card>
            </Container>


        </>
    )
}

