import { createSlice } from '@reduxjs/toolkit'

const initialState : string =  "https://port-0-stock-backend-6g2llexzc4j4.sel3.cloudtype.app"
// "http://localhost:3001"

export const localUrlSlice = createSlice({
  name: 'localUrl',
  initialState,
  reducers: {
    localUrl_input(state, action) {
      state = action.payload;
    }, 
  },
})

// Action creators are generated for each case reducer function
export const { localUrl_input } = localUrlSlice.actions

export default localUrlSlice.reducer