import { createSlice } from '@reduxjs/toolkit'


const initialState = [
    {
        '': '2023',
        '': 0,
        '10': 0,
        '20': 0,
    },{
        '': '2024',
        '': 0,
        '10': 0,
        '20': 0,
    },{
        '': '2025',
        '': 0,
        '10': 0,
        '20': 0,
    }
]

export const srimCorpValueSlice = createSlice({
    name: 'srimCorpValue',
    initialState,
    reducers: {
        srimCorpValue_input(state, action) {
            return action.payload
        },
        srimCorpValue_partInput(state, action){
            state[0] = action.payload
        },
        srimCorpValue_reset(state){
            return initialState
        }
    },
})



// Action creators are generated for each case reducer function
export const { srimCorpValue_input, srimCorpValue_partInput, srimCorpValue_reset } = srimCorpValueSlice.actions

export default srimCorpValueSlice.reducer