import { createSlice } from '@reduxjs/toolkit'

interface corpNameState  {
  value : string,
}

const initialState : corpNameState = {
  value: "",
}

export const corpNameSlice = createSlice({
  name: 'corpName',
  initialState,
  reducers: {
    corpName_input(state, action) {
      state.value = action.payload
    }, 
  },
})



// Action creators are generated for each case reducer function
export const { corpName_input } = corpNameSlice.actions

export default corpNameSlice.reducer