import React, { useState } from 'react';
import { ResponsiveBar } from '@nivo/bar'
import Container from 'react-bootstrap/Container';
import { useSelector } from "react-redux";
import { useEffect } from 'react';
import Card from 'react-bootstrap/Card';



export default function SrimStockGraph() {

    const srimStockPrice = useSelector((state) => state.srimStockPrice)
    const fetchSrim = useSelector((state) => state.fetchSrim)

    let firstData = [
        {
            "": srimStockPrice[0][''],
            "ROE ": srimStockPrice[0][''],
            "Color": "hsl(323, 70%, 50%)",
            "10% ": srimStockPrice[0]['10'],
            "10% Color": "hsl(271, 70%, 50%)",
            "20% ": srimStockPrice[0]['20'],
            "20% Color": "hsl(325, 70%, 50%)",
            " ": 0,
            " Color": "hsl(73, 70%, 50%)",
        }, {
            "": srimStockPrice[1][''],
            "ROE ": srimStockPrice[1][''],
            "Color": "hsl(323, 70%, 50%)",
            "10% ": srimStockPrice[1]['10'],
            "10% Color": "hsl(271, 70%, 50%)",
            "20% ": srimStockPrice[1]['20'],
            "20% Color": "hsl(325, 70%, 50%)",
            " ": 0,
            " Color": "hsl(73, 70%, 50%)",
        }, {
            "": srimStockPrice[2][''],
            "ROE ": srimStockPrice[2][''],
            "Color": "hsl(323, 70%, 50%)",
            "10% ": srimStockPrice[2]['10'],
            "10% Color": "hsl(271, 70%, 50%)",
            "20% ": srimStockPrice[2]['20'],
            "20% Color": "hsl(325, 70%, 50%)",
            " ": 0,
            " Color": "hsl(73, 70%, 50%)",
        }
    ]

    const [stockData, setStockData] = useState(firstData)

    useEffect(() => {

        let resultData = [
            {
                "": srimStockPrice[0][''],
                "ROE ": srimStockPrice[0][''],
                "Color": "hsl(323, 70%, 50%)",
                "10% ": srimStockPrice[0]['10'],
                "10% Color": "hsl(271, 70%, 50%)",
                "20% ": srimStockPrice[0]['20'],
                "20% Color": "hsl(325, 70%, 50%)",
                " ": Number(fetchSrim['']['']),
                " Color": "hsl(73, 70%, 50%)",
            }, {
                "": srimStockPrice[1][''],
                "ROE ": srimStockPrice[1][''],
                "Color": "hsl(323, 70%, 50%)",
                "10% ": srimStockPrice[1]['10'],
                "10% Color": "hsl(271, 70%, 50%)",
                "20% ": srimStockPrice[1]['20'],
                "20% Color": "hsl(325, 70%, 50%)",
                " ": Number(fetchSrim['']['']),
                " Color": "hsl(73, 70%, 50%)",
            }, {
                "": srimStockPrice[2][''],
                "ROE ": srimStockPrice[2][''],
                "Color": "hsl(323, 70%, 50%)",
                "10% ": srimStockPrice[2]['10'],
                "10% Color": "hsl(271, 70%, 50%)",
                "20% ": srimStockPrice[2]['20'],
                "20% Color": "hsl(325, 70%, 50%)",
                " ": Number(fetchSrim['']['']),
                " Color": "hsl(73, 70%, 50%)",
            }
        ]

        setStockData(() => resultData)
    }, [srimStockPrice]) //useEffect  state    


    const SrimStockGraph = ({ data }) => (
        <ResponsiveBar
            data={data}
            keys={[
                'ROE ',
                '10% ',
                '20% ',
                ' ',
            ]}
            indexBy=""
            margin={{ top: 50, right: 130, bottom: 50, left: 65 }}
            padding={0.3}
            groupMode="grouped"
            valueScale={{ type: 'linear' }}
            indexScale={{ type: 'band', round: true }}
            valueFormat=" >-,"
            colors={{ scheme: 'nivo' }}
            defs={[
                {
                    id: 'dots',
                    type: 'patternDots',
                    background: 'inherit',
                    color: '#38bcb2',
                    size: 4,
                    padding: 1,
                    stagger: true
                },
                {
                    id: 'lines',
                    type: 'patternLines',
                    background: 'inherit',
                    color: '#eed312',
                    rotation: -45,
                    lineWidth: 6,
                    spacing: 10
                }
            ]}
            fill={[
                {
                    match: {
                        id: 'fries'
                    },
                    id: 'dots'
                },
                {
                    match: {
                        id: 'sandwich'
                    },
                    id: 'lines'
                }
            ]}
            borderColor={{
                from: 'color',
                modifiers: [
                    [
                        'darker',
                        '1.6'
                    ]
                ]
            }}
            axisTop={null}
            axisRight={null}
            axisBottom={{
                tickSize: 5,
                tickPadding: 5,
                tickRotation: 0,
                legend: '',
                legendPosition: 'middle',
                legendOffset: 32
            }}
            axisLeft={{
                tickSize: 5,
                tickPadding: 5,
                tickRotation: 0,
                legend: '',
                legendPosition: 'middle',
                legendOffset: -50
            }}
            labelSkipWidth={12}
            labelSkipHeight={16}
            labelTextColor={{
                from: 'color',
                modifiers: [
                    [
                        'darker',
                        1.6
                    ]
                ]
            }}
            legends={[
                {
                    dataFrom: 'keys',
                    anchor: 'bottom-right',
                    direction: 'column',
                    justify: false,
                    translateX: 120,
                    translateY: 0,
                    itemsSpacing: 2,
                    itemWidth: 100,
                    itemHeight: 20,
                    itemDirection: 'left-to-right',
                    itemOpacity: 0.85,
                    symbolSize: 20,
                    effects: [
                        {
                            on: 'hover',
                            style: {
                                itemOpacity: 1
                            }
                        }
                    ]
                }
            ]}
            role="application"
            ariaLabel="Nivo bar chart demo"
            barAriaLabel={function (e) { return e.id + ": " + e.formattedValue + " in : " + e.indexValue }}
        />
    )


    return (
        <>
            <Container>
                <Card className="text-center" border="light">
                    <Card.Body>
                        <Card.Title></Card.Title>
                        <Card.Text>
                        </Card.Text>
                        <div style={{ width: 'auto', height: '400px', margin: '0 auto' }}>
                            <SrimStockGraph data={stockData} />
                        </div>
                    </Card.Body>
                </Card>

            </Container>


        </>
    )
}
