import React, { useState } from "react";
import { ResponsiveLine } from '@nivo/line'
import { useSelector } from "react-redux";
import { useEffect } from "react";
import Button from 'react-bootstrap/Button';
import Accordion from 'react-bootstrap/Accordion';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';


export default function SpreadMUsGraph() {
    //Url state 
    const localUrl = useSelector((state) => state.localUrl)
    const itemUrl10_2 = '/econ/spread/10-2/m/us'
    const itemUrl10_91 = '/econ/spread/10-91/m/us'


    //fetch  data 
    const [data, setData] = useState([])

    //   
    const [startDate, setStartDate] = useState('')
    const [endDate, setEndDate] = useState('')

    //data   
    async function testData() {
        console.log(data)
    }

    try {
        //  fetch 
        useEffect(() => {
            async function getData() {
                let fetch10_91 = await fetch(localUrl + itemUrl10_91)
                    .then((res) => res.json())

                let fetch10_2 = await fetch(localUrl + itemUrl10_2)
                    .then((res) => res.json())

                //   
                setStartDate(fetch10_91[0]['data'][0]['x'])
                setEndDate(fetch10_91[0]['data'][fetch10_91[0]['data'].length - 1]['x'])

                //  
                fetch10_91[0]['color'] = 'hsl(243, 70%, 50%)'
                fetch10_2[0]['color'] = 'hsl(238, 70%, 50%)'

                const result = [
                    fetch10_91[0],
                    fetch10_2[0],
                ]

                setData(result)
            };
            getData()
        }, [])

    } catch (err) {
        console.log('10-2   ')
    }

    const SpreadMUsGraph

        = ({ data }) => (
            <ResponsiveLine
                data={data}
                margin={{ top: 60, right: 140, bottom: 100, left: 60 }}
                xScale={{ type: 'point' }}
                yScale={{
                    type: 'linear',
                    min: 'auto',
                    max: 'auto',
                    stacked: false,
                    reverse: false
                }}
                yFormat=" >-.2f"
                axisTop={null}
                axisRight={null}
                axisBottom={{
                    orient: 'bottom',
                    tickSize: 5,
                    tickPadding: 5,
                    tickRotation: -80,
                    legend: '',
                    legendOffset: 50,
                    legendPosition: 'middle'
                }}
                axisLeft={{
                    orient: 'left',
                    tickSize: 5,
                    tickPadding: 5,
                    tickRotation: 0,
                    legend: '',
                    legendOffset: -40,
                    legendPosition: 'middle'
                }}
                colors={{ scheme: 'category10' }}
                lineWidth={4}
                pointSize={6}
                pointColor={{ from: 'color', modifiers: [] }}
                pointBorderWidth={2}
                pointBorderColor={{ from: 'serieColor' }}
                pointLabelYOffset={-12}
                enableSlices="x"
                useMesh={true}
                legends={[
                    {
                        anchor: 'bottom-right',
                        direction: 'column',
                        justify: false,
                        translateX: 100,
                        translateY: 0,
                        itemsSpacing: 0,
                        itemDirection: 'left-to-right',
                        itemWidth: 80,
                        itemHeight: 20,
                        itemOpacity: 0.75,
                        symbolSize: 12,
                        symbolShape: 'circle',
                        symbolBorderColor: 'rgba(0, 0, 0, .5)',
                        effects: [
                            {
                                on: 'hover',
                                style: {
                                    itemBackground: 'rgba(0, 0, 0, .03)',
                                    itemOpacity: 1
                                }
                            }
                        ]
                    }
                ]}
            />
        )
    return (
        <>

            <Container>
                <Card border="light">
                    <Card.Body>
                        <Card.Title className="text-center">   [3]</Card.Title>
                        <Card.Text className="text-center">
                            {startDate} ~ {endDate}
                        </Card.Text>
                        {/* <Button variant="primary" onClick={testData}>Primary</Button>{' '} */}
                        <div style={{ width: 'auto', height: '400px', margin: '0 auto' }}>
                            <SpreadMUsGraph data={data} />
                        </div>
                        <Row>
                            <Col></Col>
                            <Col lg="8">
                                <Accordion>
                                    <Accordion.Item eventKey="0">
                                        <Accordion.Header></Accordion.Header>
                                        <Accordion.Body>
                                                 . <br />
                                                      . <br />
                                                   , <br />
                                                      .<br />
                                                             , <br />
                                              1960          .<br />
                                                    10 2.<br />
                                              10 3 .
                                        </Accordion.Body>
                                    </Accordion.Item>
                                    <Accordion.Item eventKey="1">
                                        <Accordion.Header> </Accordion.Header>
                                        <Accordion.Body>
                                         (FRED, Federal Reserve Economic Data) <br />
                                        10-Year Treasury Constant Maturity Minus 2-Year Treasury Constant Maturity (T10Y2Y)<br />
                                        10-Year Treasury Constant Maturity Minus 3-Month Treasury Constant Maturity (T10Y3M)
                                        </Accordion.Body>
                                    </Accordion.Item>
                                </Accordion>
                            </Col>
                            <Col></Col>
                        </Row>
                    </Card.Body>
                </Card>
            </Container>
        </>
    )
}