import React, { useState } from "react";
import { ResponsiveLine } from '@nivo/line'
import { useSelector } from "react-redux";
import { useEffect } from "react";
import Accordion from 'react-bootstrap/Accordion';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';


export default function EsiNsiGraph() {
    const localUrl = useSelector((state) => state.localUrl)
    const itemUrl = '/econ/bok/esi_nsi'

    const [data, setData] = useState([])

    //   
    const [startDate, setStartDate] = useState('')
    const [endDate, setEndDate] = useState('')

    try {
        useEffect(() => {
            async function getEsiNsi() {
                let result = await fetch(localUrl + itemUrl)
                    .then((res) => res.json())

                //   
                setStartDate(result[0]['data'][0]['x'])
                setEndDate(result[0]['data'][result[0]['data'].length - 1]['x'])

                result[0]['color'] = 'hsl(50, 70%, 50%)'
                result[1]['color'] = 'hsl(328, 70%, 50%)'

                setData(result)
            };
            getEsiNsi()
        }, [])

    } catch (err) {
        console.log(',   ')
    }





    const EsiNsiGraph = ({ data }) => (
        <ResponsiveLine
            data={data}
            margin={{ top: 50, right: 110, bottom: 100, left: 60 }}
            xScale={{ type: 'point' }}
            yScale={{
                type: 'linear',
                min: 'auto',
                max: 'auto',
                stacked: false,
                reverse: false
            }}
            yFormat=" >-.2f"
            axisTop={null}
            axisRight={null}
            axisBottom={{
                orient: 'bottom',
                tickSize: 5,
                tickPadding: 5,
                tickRotation: -80,
                legend: '',
                legendOffset: 50,
                legendPosition: 'middle'
            }}
            axisLeft={{
                orient: 'left',
                tickSize: 5,
                tickPadding: 5,
                tickRotation: 0,
                legend: '',
                legendOffset: -40,
                legendPosition: 'middle'
            }}
            colors={{ scheme: 'category10' }}
            lineWidth={3}
            pointSize={5}
            pointColor={{ from: 'color', modifiers: [] }}
            pointBorderWidth={2}
            pointBorderColor={{ from: 'serieColor' }}
            pointLabelYOffset={-12}
            enableSlices="x"
            useMesh={true}
            legends={[
                {
                    anchor: 'bottom-right',
                    direction: 'column',
                    justify: false,
                    translateX: 100,
                    translateY: 0,
                    itemsSpacing: 0,
                    itemDirection: 'left-to-right',
                    itemWidth: 80,
                    itemHeight: 20,
                    itemOpacity: 0.75,
                    symbolSize: 12,
                    symbolShape: 'circle',
                    symbolBorderColor: 'rgba(0, 0, 0, .5)',
                    effects: [
                        {
                            on: 'hover',
                            style: {
                                itemBackground: 'rgba(0, 0, 0, .03)',
                                itemOpacity: 1
                            }
                        }
                    ]
                }
            ]}
        />
    )
    return (
        <>

            <Container>
                <Card border="light">
                    <Card.Body>
                        <Card.Title className="text-center">  [5]</Card.Title>
                        <Card.Text className="text-center">
                            {startDate} ~ {endDate}
                        </Card.Text>
                        <div style={{ width: 'auto', height: '400px', margin: '0 auto' }}>
                            <EsiNsiGraph data={data} />
                        </div>
                        <Row>
                            <Col></Col>
                            <Col lg="8">
                                <Accordion>
                                    <Accordion.Item eventKey="0">
                                        <Accordion.Header></Accordion.Header>
                                        <Accordion.Body>
                                            (ESI, Economic Sentiment Index)                .<br />
                                             2012 6  , 2003 1 2012 12 ESI  100 . <br />
                                              100      , 100     .
                                        </Accordion.Body>
                                    </Accordion.Item>
                                    <Accordion.Item eventKey="1">
                                        <Accordion.Header></Accordion.Header>
                                        <Accordion.Body>
                                            (NSI, News Sentiment Index)         , <br />
                                                        . <br/>
                                             100    , 100     .
                                        </Accordion.Body>
                                    </Accordion.Item>
                                    <Accordion.Item eventKey="2">
                                        <Accordion.Header> </Accordion.Header>
                                        <Accordion.Body>
                                              <br/>
                                            [513Y001] [E2000] <br/>
                                            [521Y001] [A001]
                                        </Accordion.Body>
                                    </Accordion.Item>
                                </Accordion>
                            </Col>
                            <Col></Col>
                        </Row>
                    </Card.Body>
                </Card>
            </Container>
        </>
    )
}