import { createSlice } from '@reduxjs/toolkit'

const initialState : string =  "https://port-0-stock-backend-6g2llexzc4j4.sel3.cloudtype.app"

export const backEndUrlSlice = createSlice({
  name: 'backEndUrl',
  initialState,
  reducers: {
    backEndUrl_input(state, action) {
      state = action.payload;
    }, 
  },
})

// Action creators are generated for each case reducer function
export const { backEndUrl_input } = backEndUrlSlice.actions

export default backEndUrlSlice.reducer