import { createSlice } from '@reduxjs/toolkit'



const initialState : any = {
    status: "",
    message: "",
    corp_code: "",
    corp_name: "",
    corp_name_eng: "",
    stock_name: "",
    stock_code: "",
    ceo_nm: "",
    corp_cls: "",
    jurir_no: "",
    bizr_no: "",
    adres: "",
    hm_url: "",
    ir_url: "",
    phn_no: "",
    fax_no: "",
    induty_code: "",
    est_dt: "",
    acc_mt: ""
}

export const overViewSlice = createSlice({
  name: 'overView',
  initialState,
  reducers: {
    overView_input(state, action) {
        return action.payload
    }, 
  },
})



// Action creators are generated for each case reducer function
export const { overView_input } = overViewSlice.actions

export default overViewSlice.reducer