import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import ListGroup from 'react-bootstrap/ListGroup';
import Container from "react-bootstrap/esm/Container";
import Row from "react-bootstrap/esm/Row";
import Col from "react-bootstrap/esm/Col";


export default function OverView() {

    const overView = useSelector((state: any) => state.overView)

    const [, set] = useState('')
    const [, set] = useState('')
    const [, set] = useState('')
    const [IR, setIR] = useState('')
    const [, set] = useState('')
    const [, set] = useState('')

    const [linkActivated, setLinkActivated] = useState<boolean>(true)
    const [irLinkActivated, setIrLinkActivated] = useState<boolean>(true)


    useEffect(() => {
        if (overView.corp_cls === 'Y') {
            set('()')
        } else if (overView.corp_cls === 'K') {
            set('')
        } else if (overView.corp_cls === 'N') {
            set('')
        } else {
            set(' or ')
        }

        if (overView.est_dt !== '') {
            set(() => overView.est_dt.slice(0, 4) + '-' + overView.est_dt.slice(4,6) + '-' + overView.est_dt.slice(6) )
        }

        if (overView.jurir_no !== '' && overView.jurir_no.length === 13) {
            set(() => overView.jurir_no.slice(0, 6) + '-' + overView.jurir_no.slice(6))
        } else {
            set(()=>overView.jurir_no)
        }

        if (overView.bizr_no !== '' && overView.bizr_no.length === 10) {
            set(() => overView.bizr_no.slice(0, 3) + '-' + overView.bizr_no.slice(3, 5) + '-' + overView.bizr_no.slice(5))
        } else {
            set(()=>overView.bizr_no)
        }

        if (overView.hm_url === '' || overView.hm_url === '_') {
            
            setLinkActivated(() => true)
        } else {
            if (overView.hm_url.slice(0, 5) === 'https') {
                set(() => overView.hm_url)
                setLinkActivated(() => false)
            } else {
                set(() => 'https:/' + overView.hm_url)
                setLinkActivated(() => false)
            }
        }

        if (overView.ir_url !== '') {
            if (overView.ir_url.slice(0, 5) === 'https') {
                setIR(() => overView.ir_url)
                setIrLinkActivated(() => false)
            } else {
                setIR(() => 'https:/' + overView.ir_url)
                setIrLinkActivated(() => false)
            }
        } else {
            setIrLinkActivated(() => true)
        }


    }, [overView])
    return (
        <>
            <Container>
                <Row>
                    <Col>
                        <Card border="light">
                            <Card.Body>
                                <Card.Title>{overView.stock_name}</Card.Title>
                                <Card.Subtitle className="mb-3 text-muted">{overView.corp_name_eng}</Card.Subtitle>
                                <Button variant="outline-dark" onClick={() => { window.open(, '_blank') }} disabled={linkActivated}></Button>
                                &nbsp; &nbsp; &nbsp;
                                <Button variant="outline-dark" onClick={() => { window.open(IR, '_blank') }} disabled={irLinkActivated}>IR PAGE</Button>
                            </Card.Body>

                            <Container>
                                <Row>
                                    <Col lg='6'>
                                        <Card>
                                            <ListGroup className="list-group-flush">
                                                <ListGroup.Item><small className="text-muted">:</small> {overView.ceo_nm}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">:</small> {overView.adres}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">:</small> {}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">:</small> {overView.phn_no}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">:</small> {}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">:</small> {overView.acc_mt}</ListGroup.Item>
                                            </ListGroup>
                                        </Card>
                                    </Col>
                                    <Col lg='6'>
                                        <Card>
                                            <ListGroup variant="flush">
                                                <ListGroup.Item><small className="text-muted">:</small> {overView.stock_code}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">:</small> {overView.corp_code}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">:</small> {}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">:</small> {}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">:</small> {overView.fax_no}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">:</small> {overView.induty_code}</ListGroup.Item>
                                            </ListGroup>
                                        </Card>
                                    </Col>
                                </Row>
                                <br />
                            </Container>


                        </Card>
                    </Col>
                </Row>
            </Container>

        </>
    )
}