import React, { useState } from "react";
import { ResponsiveLine } from '@nivo/line'
import { useSelector } from "react-redux";
import { useEffect } from "react";
import Button from 'react-bootstrap/Button';
import Accordion from 'react-bootstrap/Accordion';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';


export default function BuffettGraph() {
    //Url state 
    const localUrl = useSelector((state) => state.localUrl)
    const itemUrlKR = '/econ/buffett_index/kr'
    const itemUrlUS = '/econ/buffett_index/us'

    //fetch  data 
    const [data, setData] = useState([])

    //   
    const [startDate, setStartDate] = useState('')
    const [endDate, setEndDate] = useState('')

    //data   
    async function testData() {
        console.log(data)
    }

    try {
        //  fetch 
        useEffect(() => {
            async function getData() {
                let fetchKR = await fetch(localUrl + itemUrlKR)
                    .then((res) => res.json())

                let fetchUS = await fetch(localUrl + itemUrlUS)
                    .then((res) => res.json())

                //   
                setStartDate(fetchKR[0]['data'][0]['x'])
                setEndDate(fetchUS[0]['data'][fetchUS[0]['data'].length - 1]['x'])

                //  
                fetchKR[0]['color'] = 'hsl(158, 70%, 50%)'
                fetchUS[0]['color'] = 'hsl(20, 70%, 50%)'

                const result = [
                    fetchKR[0],
                    fetchUS[0],
                ]

                setData(result)
            };
            getData()
        }, [])

    } catch (err) {
        console.log('Vix (3)  ')
    }

    const BuffettGraph

        = ({ data }) => (
            <ResponsiveLine
                data={data}
                margin={{ top: 60, right: 110, bottom: 100, left: 60 }}
                xScale={{ type: 'point' }}
                yScale={{
                    type: 'linear',
                    min: 'auto',
                    max: 'auto',
                    stacked: false,
                    reverse: false
                }}
                yFormat=" >-.2f"
                axisTop={null}
                axisRight={null}
                axisBottom={{
                    orient: 'bottom',
                    tickSize: 5,
                    tickPadding: 5,
                    tickRotation: -80,
                    legend: '',
                    legendOffset: 50,
                    legendPosition: 'middle'
                }}
                axisLeft={{
                    orient: 'left',
                    tickSize: 5,
                    tickPadding: 5,
                    tickRotation: 0,
                    legend: '',
                    legendOffset: -40,
                    legendPosition: 'middle'
                }}
                colors={{ scheme: 'category10' }}
                lineWidth={4}
                pointSize={6}
                pointColor={{ from: 'color', modifiers: [] }}
                pointBorderWidth={2}
                pointBorderColor={{ from: 'serieColor' }}
                pointLabelYOffset={-12}
                enableSlices="x"
                useMesh={true}
                legends={[
                    {
                        anchor: 'bottom-right',
                        direction: 'column',
                        justify: false,
                        translateX: 100,
                        translateY: 0,
                        itemsSpacing: 0,
                        itemDirection: 'left-to-right',
                        itemWidth: 80,
                        itemHeight: 20,
                        itemOpacity: 0.75,
                        symbolSize: 12,
                        symbolShape: 'circle',
                        symbolBorderColor: 'rgba(0, 0, 0, .5)',
                        effects: [
                            {
                                on: 'hover',
                                style: {
                                    itemBackground: 'rgba(0, 0, 0, .03)',
                                    itemOpacity: 1
                                }
                            }
                        ]
                    }
                ]}
            />
        )
    return (
        <>

            <Container>
                <Card border="light" >
                    <Card.Body>
                        <Card.Title className="text-center">   [10]</Card.Title>
                        <Card.Text className="text-center">
                            {startDate} ~ {endDate}
                        </Card.Text>
                        {/* <Button variant="primary" onClick={testData}>Primary</Button>{' '} */}
                        <div style={{ width: 'auto', height: '400px', margin: '0 auto' }}>
                            <BuffettGraph data={data} />
                        </div>
                        <Row>
                            <Col></Col>
                            <Col lg="8">
                                <Accordion>
                                    <Accordion.Item eventKey="0">
                                        <Accordion.Header></Accordion.Header>
                                        <Accordion.Body>
                                              (GDP)      /(GDP)*100 . <br />
                                            (GDP, gross domestic product)      ,      . <br />
                                                     ,    80~100%     , <br />
                                            80%    , 100%      (bubble)  .
                                        </Accordion.Body>
                                    </Accordion.Item>
                                    <Accordion.Item eventKey="1">
                                        <Accordion.Header> </Accordion.Header>
                                        <Accordion.Body>
                                              100%       . <br />
                                              20             .<br />
                                                GDP      .<br />
                                            <br />
                                               GDP   GDP   . <br />
                                             GDP   GDP        , GDP      .<br />
                                            ,     GDP    ,    GDP  .<br />
                                                       Wilshire 5000 Full Cap Price Index .
                                        </Accordion.Body>
                                    </Accordion.Item>
                                    <Accordion.Item eventKey="2">
                                        <Accordion.Header> </Accordion.Header>
                                        <Accordion.Body>
                                             (FRED, Federal Reserve Economic Data) <br />
                                            Gross Domestic Product (GDP)<br />
                                            Wilshire 5000 Full Cap Price Index (WILL5000PRFC)<br />
                                            <br />
                                              <br />
                                             GDP  GNI(, , )[200Y003] [1400] <br />
                                            [901Y014] KOSPI_[1040000], KOSDAQ_[2040000]
                                        </Accordion.Body>
                                    </Accordion.Item>
                                </Accordion>
                            </Col>
                            <Col></Col>
                        </Row>
                    </Card.Body>
                </Card>

            </Container>
        </>
    )
}