import { configureStore } from '@reduxjs/toolkit'
import counterReducer from './counter/counterSlice'
import corpNameReducer from './corpSlice/corpNameSlice'
import overViewReducer from './corpSlice/overViewSlice'
import localUrlReducer from './apiUrlSlice/localUrlSlice'
import backEndUrlReducer from './apiUrlSlice/backEndUrlSlice'
import fetchSrimReducer from './srimSlice/fetchSrimSlice'
import srimCorpValueReducer from './srimSlice/srimCorpValueSlice'
import srimStockPriceReducer from './srimSlice/srimStockPriceSlice'

export const store = configureStore({
  reducer: {
    counter: counterReducer,
    corpName: corpNameReducer,
    overView: overViewReducer,
    localUrl: localUrlReducer,
    backEndUrl: backEndUrlReducer,
    fetchSrim: fetchSrimReducer,
    srimCorpValue: srimCorpValueReducer,
    srimStockPrice: srimStockPriceReducer
  },
})